
.paginator {
  display: flex;
  justify-content: center;
  user-select: none;
  margin: 10px;
}
.pageNumber {
  padding: 2px;
  border: 1px solid grey;
}

.pageNumber.selectedPage {
  font-weight: bold;
}

.navBtn{
  width: 27px;
  height: 27px;
  img{
    width: 100%;
    height: 100%;
  }
}
.right{
  margin-left: 10px;
}
.left{
  margin-right: 10px;
}